@charset "UTF-8";
@import url(https://fonts.googleapis.com/css?family=Roboto:regular,700,900);
body {
    font-family: "Roboto", sans-serif;
    font-weight: normal;
    color: #fff;
}

* {
    box-sizing: border-box;
}

html, body {
    height: 100%;
}

body {
    background-color: grey;
}

[class*=__container] {
    max-width: 1240px;
    margin: 0 auto;
    padding: 0 15px;
}

header {
    width: 100%;
    background-color: #44687f;
}

.hamburger {
    display: none;
}

.navigation {
    display: flex;
}
.navigation__list {
    width: 945px;
    display: flex;
    justify-content: space-between;
}
.navigation__list .sublevel-1 {
    opacity: 0;
    visibility: hidden;
    position: absolute;
    top: 100%;
    left: 0;
}
.navigation__item {
    position: relative;
    padding: 30px;
    transition: background 0.5s ease 0s;
}
.navigation__item:hover {
    background-color: #c2e3f9;
}
.navigation__item:hover .sublevel-1 {
    opacity: 1;
    visibility: visible;
}
.navigation__link {
    color: #fff;
    font-size: 13px;
    font-weight: 700;
    text-transform: uppercase;
}
.navigation__link:hover {
    color: #000;
}

.active {
    background-color: #35a2eb;
}

.arrow {
    color: #fff;
    padding-left: 5px;
}

.opener {
    display: inline-block;
    font: 14px/1 font-awesome;
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    padding-right: 20px;
}
.opener:hover {
    background-color: #c2e3f9;
}

.navigation__list--sub {
    width: 200px;
    background-color: #c2e3f9;
}
.navigation__list--sub .sublevel-2 {
    opacity: 0;
    visibility: hidden;
    position: absolute;
    top: 100%;
    left: 0;
}

.navigation__list--sub .navigation__item {
    padding: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    background-color: #35a2eb;
    margin-bottom: 2px;
}
.navigation__list--sub .navigation__item:hover {
    background-color: #c2e3f9;
}
.navigation__list--sub .navigation__item:hover .sublevel-2 {
    opacity: 1;
    visibility: visible;
}

.navigation__list--sub .navigation__link {
    padding: 20px 57px 20px 18px;
}

@media (max-width: 768px) {
    .hamburger {
        display: flex;
    }
    .navigation {
        display: none;
    }
}
.navigation__list--sub .navigation__item {
    flex-wrap: wrap;
}
.navigation__list--sub .navigation__link {
    display: flex;
    padding-right: 35px;
    position: relative;
    width: 100%;
}
.navigation__list--sub .navigation__link:hover .opener {
    color: white;
}
.navigation__list--sub .opener {
    position: absolute;
    right: 0;
    top: 0;
    height: 100%;
    width: 30px;
    background: #44687f;
}
.navigation__list--sub .opener:hover {
    color: #35a2eb;
    background: cyan;
}
.navigation__list--sub .opener:before {
    content: "+";
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}
.navigation__list--sub .open-close--active .navigation__link {
    background: #35a2eb;
    color: white;
}
.navigation__list--sub .open-close--active .opener {
    color: #35a2eb;
    background: cyan;
}
.navigation__list--sub .open-close--active .opener:before {
    content: "-";
}

.navigation__list--sub .sublevel-2 {
    opacity: 1;
    visibility: visible;
    position: relative;
}

